import GLOBAL from "../../../service.js"
import {uniCard} from '@dcloudio/uni-ui'
export default {   
	components: {uniCard},
	data() {
		return {
			id: '',
			news:'',
			title: "评论界面",
			comment_data:[],
			comment:"",
			userId:''
		}
	},
	onLoad(options)
	{
		this.userId=uni.getStorageSync("userData").uno,
		this.id = options.id,
		console.log(this.id),
		uni.request({
			url:GLOBAL.BASE+'/getIDnews/'+this.id+'',
			success: (res) => {
				this.news=res.data;
				console.log(this.news)
			}
		}),
		uni.request({
			url:GLOBAL.BASE+'/getComment?NewsId='+this.id+'',
			success: (res) =>{
				this.comment_data = res.data,
				console.log(this.comment_data)
			}
		})
		
	},
	methods: {
			comment_upload(e){
			  uni.request({
			  	url: GLOBAL.BASE+'/isAlive',
				header:{
					"Cookie":"JSESSIONID="+uni.getStorageSync("sid")
				},
			  	success: res => {
					console.log(res.data);
					if(res.data==true){
						this.comment = e.detail.value,
						uni.request({
							url:GLOBAL.BASE+'/comment?content='+this.comment+'&news_id='+this.id+'',
								header:{
								"Cookie":"JSESSIONID="+uni.getStorageSync("sid")
							}
						})
						var news_item={
						  com_id: 1,
						  content: this.comment,
						  news_id:this.id,
						  time: '今天',
						  user_id: this.userId
						}
						this.comment_data.push(news_item)
						this.comment=''
					}
					else{
						uni.clearStorage('sid');
						console.log("sid失效");
						uni.navigateTo({
							url:'../test'
						})
					}
				},
			  	fail: () => {}
			  });
			  // else{
			 //  this.comment = e.detail.value,
			 //  uni.request({
			 //  	url:GLOBAL.BASE+'/comment?content='+this.comment+'&news_id='+this.id+'',
				// header:{
				// 	"Cookie":"JSESSIONID="+uni.getStorageSync("sid")
				// }
			 //  })
			 //  var news_item={
				//   com_id: 1,
				//   content: this.comment,
				//   news_id:this.id,
				//   time: '今天',
				//   user_id: this.userId
			 //  }
			 //  this.comment_data.push(news_item)
			 //  this.comment=''
			  // }
		  }
		  
	}
}
